<!--
 * @Description: Vue实现自定义右键菜单 页面
 * @Author: mhf
 * @Date: 2023-05-13 20:07:23
 * @Visit: https://github.com/Kiyoaki-w/Ki-vue-context#readme (官网)
 * https://fontawesome.com/
-->
<template>
  <div
    class="right-menu"
    @click="hideContextMenu()"
    @contextmenu.prevent="showContextMenu($event)"
  >
    <back-home-btn />
    <!-- 在适当位置插入ki-context组件 -->
    <ki-context
      ref="kiContext"
      minWidth="1em"
      maxWidth="15em"
      backgroundColor="#fbfbfb"
      fontSize="15px"
      textColor="#35495e"
      iconColor="#41b883"
      borderRadius="0.1"
    />
    <!-- 以上属性都是可以自定义的 -->
  </div>
</template>

<script>
export default {
  name: "vueRightMenu",
  methods: {
    showContextMenu(event) {
      // 在items数组中定义所需的右键菜单选项
      let items = [
        {
          icon: "heart",
          text: "刷新",
          click: () => {
            this.reLoad();
          },
        },
        {
          icon: "users",
          text: "this is users",
          divider: true,
          click: () => {
            console.log("users");
          },
        },
        {
          icon: "file-import",
          text: "fileImport is disabled",
          disabled: true,
          click: () => {
            console.log("fileImport");
          },
        },
        {
          icon: "file-excel",
          text: "this is fileExcel",
          divider: true,
          click: () => {
            console.log("fileExcel");
          },
        },
      ];
      this.$refs.kiContext.show(event, items);
    },
    hideContextMenu() {
      this.$refs.kiContext.hide();
    },

    reLoad() {
      // this.$router.go(-1)// 后退+刷新；
      // this.$router.go(n) // 前进n个页面
      // this.$router.go(0)// 刷新
      location.reload();
    },
  },
};
</script>

<style lang="scss">
.right-menu {
  width: 100vw;
  height: 100vh;
  margin: -2px;
  padding: 0;
}
</style>
